.todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .Si {
    display: flex;
    flex-direction: row;
    text-decoration: line-through;
  }
  
  .No {
    display: flex;
    flex-direction: row;
    text-decoration: none;
  }
  